/* Product Sales Analytics - Professional Styling with CSS Variables Only */

.product-sales-page {
  max-width: 1600px;
  margin: 0 auto;
  padding: 0;
}

.ps-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.ps-title {
  font-size: 2rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.ps-date-selector {
  display: flex;
  gap: 0.75rem;
  align-items: center;
  font-size: 0.875rem;
  color: var(--text-secondary);
}

.ps-date-input {
  padding: 0.625rem 0.875rem;
  border: 1px solid var(--border);
  border-radius: 6px;
  background: var(--bg-surface);
  color: var(--text-primary);
  font-size: 0.875rem;
  transition: border-color 0.2s ease;
}

.ps-date-input:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(46, 125, 50, 0.1);
}

/* KPI Cards */
.ps-kpi-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 1.25rem;
  margin-bottom: 2rem;
}

.ps-kpi-card {
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: 10px;
  padding: 1.5rem;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  position: relative;
  overflow: hidden;
}

.ps-kpi-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: var(--primary);
  opacity: 0;
  transition: opacity 0.2s ease;
}

.ps-kpi-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
}

.ps-kpi-card:hover::before {
  opacity: 1;
}

.ps-kpi-label {
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--text-secondary);
  margin-bottom: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.ps-kpi-value {
  font-size: 2.25rem;
  font-weight: 700;
  color: var(--text-primary);
  line-height: 1;
}

.ps-kpi-sublabel {
  font-size: 0.75rem;
  color: var(--text-muted);
  margin-top: 0.5rem;
}

/* Filters Section */
.ps-filters-section {
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: 10px;
  padding: 1.5rem;
  margin-bottom: 2rem;
}

.ps-search-row {
  margin-bottom: 1.25rem;
}

.ps-search-input {
  width: 100%;
  padding: 0.75rem 1rem;
  border: 1px solid var(--border);
  border-radius: 8px;
  background: var(--bg-main);
  color: var(--text-primary);
  font-size: 0.9375rem;
  transition: all 0.2s ease;
}

.ps-search-input:focus {
  outline: none;
  border-color: var(--primary);
  background: var(--bg-surface);
  box-shadow: 0 0 0 3px rgba(46, 125, 50, 0.08);
}

.ps-search-input::placeholder {
  color: var(--text-muted);
}

.ps-filters-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.25rem;
}

.ps-filter-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.ps-filter-label {
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--text-secondary);
}

.ps-filter-select,
.ps-filter-input {
  padding: 0.625rem 0.875rem;
  border: 1px solid var(--border);
  border-radius: 6px;
  background: var(--bg-main);
  color: var(--text-primary);
  font-size: 0.875rem;
  transition: border-color 0.2s ease;
}

.ps-filter-select:focus,
.ps-filter-input:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(46, 125, 50, 0.08);
}

.ps-checkbox-group {
  display: flex;
  align-items: center;
  gap: 0.625rem;
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid var(--divider);
}

.ps-checkbox {
  width: 18px;
  height: 18px;
  cursor: pointer;
  accent-color: var(--primary);
}

/* Table Section */
.ps-table-section {
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: 10px;
  overflow: hidden;
}

.ps-table-wrapper {
  overflow-x: auto;
}

.ps-table {
  width: 100%;
  border-collapse: collapse;
}

.ps-table thead {
  background: var(--bg-main);
  border-bottom: 2px solid var(--border);
}

.ps-table th {
  padding: 1rem 1.25rem;
  text-align: left;
  font-size: 0.8125rem;
  font-weight: 700;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  white-space: nowrap;
}

.ps-table th.sortable {
  cursor: pointer;
  user-select: none;
  transition: background-color 0.15s ease;
}

.ps-table th.sortable:hover {
  background: var(--bg-hover);
  color: var(--primary);
}

.ps-table tbody tr {
  border-bottom: 1px solid var(--border);
  transition: background-color 0.15s ease;
}

.ps-table tbody tr:hover {
  background: var(--bg-hover);
}

.ps-table tbody tr:last-child {
  border-bottom: none;
}

.ps-table td {
  padding: 1rem 1.25rem;
  font-size: 0.9375rem;
  color: var(--text-primary);
  vertical-align: middle;
}

.ps-product-name {
  font-weight: 600;
  color: var(--primary);
  cursor: pointer;
  transition: color 0.15s ease;
}

.ps-product-name:hover {
  color: var(--primary-dark);
  text-decoration: underline;
}

/* Status Badges */
.ps-status-badge {
  display: inline-block;
  padding: 0.375rem 0.75rem;
  border-radius: 16px;
  font-size: 0.8125rem;
  font-weight: 600;
  text-align: center;
  line-height: 1;
}

.instock {
  background: rgba(46, 125, 50, 0.12);
  color: var(--success);
}

.lowstock {
  background: rgba(237, 108, 2, 0.12);
  color: var(--warning);
}

.outofstock {
  background: rgba(211, 47, 47, 0.12);
  color: var(--error);
}

/* Empty State */
.ps-empty-state {
  text-align: center;
  padding: 4rem 2rem;
  color: var(--text-muted);
}

.ps-empty-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
  opacity: 0.5;
}

.ps-empty-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-secondary);
  margin-bottom: 0.5rem;
}

/* Pagination */
.ps-pagination {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.25rem 1.5rem;
  border-top: 1px solid var(--border);
  background: var(--bg-main);
}

.ps-pagination-info {
  font-size: 0.875rem;
  color: var(--text-secondary);
  font-weight: 500;
}

.ps-pagination-controls {
  display: flex;
  gap: 0.75rem;
}

.ps-pagination-btn {
  padding: 0.625rem 1.25rem;
  border: 1px solid var(--border);
  border-radius: 6px;
  background: var(--bg-surface);
  color: var(--text-primary);
  cursor: pointer;
  font-size: 0.875rem;
  font-weight: 600;
  transition: all 0.2s ease;
}

.ps-pagination-btn:hover:not(:disabled) {
  background: var(--primary);
  color: white;
  border-color: var(--primary);
  transform: translateY(-1px);
}

.ps-pagination-btn:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

/* Loading Skeletons */
.ps-skeleton {
  background: linear-gradient(
    90deg,
    var(--bg-hover) 25%,
    var(--bg-surface) 50%,
    var(--bg-hover) 75%
  );
  background-size: 200% 100%;
  animation: ps-loading 1.5s ease-in-out infinite;
  border-radius: 6px;
}

@keyframes ps-loading {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}

.ps-skeleton-card {
  height: 120px;
}

.ps-skeleton-row {
  height: 60px;
  margin-bottom: 0.75rem;
}

/* Error State */
.ps-error {
  background: rgba(211, 47, 47, 0.1);
  border: 1px solid var(--error);
  border-radius: 8px;
  padding: 1.5rem;
  margin-bottom: 2rem;
  color: var(--error);
  font-weight: 500;
}

/* Responsive Design */
@media (max-width: 768px) {
  .ps-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .ps-title {
    font-size: 1.5rem;
  }

  .ps-kpi-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .ps-filters-grid {
    grid-template-columns: 1fr;
  }

  .ps-table-wrapper {
    overflow-x: scroll;
  }

  .ps-table th,
  .ps-table td {
    padding: 0.75rem 1rem;
    font-size: 0.8125rem;
  }

  .ps-pagination {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }

  .ps-pagination-controls {
    width: 100%;
    justify-content: space-between;
  }

  .ps-pagination-btn {
    flex: 1;
  }
}
